import React from 'react'
import './EditPage.less'
import { Layout, Divider, Spin } from "antd";
import HeadBarView from "@/components/HeadBar";
import DataShowView from './DataShow';
import {
    FunnelPlotOutlined,
    BarChartOutlined,
    CaretRightOutlined,
    CaretLeftOutlined,
} from '@ant-design/icons';
import { filter } from 'lodash';
import VideoShow from './VideoShow'
import VideoSliceCardList from './VideoSliceCardList'
import LeftSidebar from './LeftSidebar'
import ActionType from '@/action/type';
import TimeLine from './TimeLine'
import 'react-timeline-bar/dist/index.css'
import Timeline from 'react-timeline-bar'
import 'react-timeline-bar/dist/index.css'
import TimelineRuler from './TimeLine/TimelineRuler';
import TalkBottom from './TalkBottomView';

const { Header } = Layout;
const { Footer, Sider } = Layout;


export default class EditPage extends React.Component {
    constructor(props) {
        super(props);
        let lang = props.intl.options.currentLocale
        filter(lang);

        this.state = {
            collapaseLeft: false,
            collapaseRight: false,
            focusMenu: ActionType.SET_DEFAULT,
            videoPlaying: false,
            updateEdit: false,
        }
    }

    UNSAFE_componentWillMount() {
        let datares = _.cloneDeep(this.props.dataresLocal);
        let schemaFilter = _.cloneDeep(this.props.schemaFilterLocal);
        let filename = _.cloneDeep(this.props.fileName);
        this.props.initialVideoSlice(this.props.facts, schemaFilter, filename, datares, this.props.schema)
    }

    //topic
    isUpdateEditing = (updateEdit) => {
        this.setState({
            updateEdit: updateEdit
        })
    }

    //
    foldLeftPanel = () => {
        this.setState(state => ({ collapaseLeft: !state.collapaseLeft }))
    }

    //
    foldRightPanel = () => {
        this.setState(state => ({ collapaseRight: !state.collapaseRight }))
    }


    changeFocusMenu = (content) => {
        this.props.setLeftsiderContent(content)
    }
    //
    onMouseUp = (e) => {
        //TODO
        if (!this.props.isMouseDown) return
        this.props.setMouseDownState(false)
        this.props.setCurrentBarIndex(-1)
        this.props.setDragCursor('default')
    }



    render() {
        const { intl, data, initDone, leftContent } = this.props
        const { collapaseLeft, collapaseRight } = this.state
        let tip = initDone && intl.get("Generating...");
        


        // 
        return (
            <div className='datavideos-edit-page'>
                <Header style={{ height: "50px" }}>
                    <HeadBarView isLogIn={false} {...this.props} />
                </Header>
                <Divider className="customDivider" />
                <Spin tip={tip} spinning={this.state.updateEdit} wrapperClassName="allSpining">
                    <Layout className="content">
        
                        <div className='content-edit'>
                            <Layout className='edit-video'>

                                {/*  */}
                                <div className='left-content'>
                                    <Sider
                                        className='left-sider'
                                        collapsed={collapaseLeft}
                                        collapsedWidth={0}
                                        width={310}>
                                        <LeftSidebar intl={intl} initDone={initDone} isUpdateEditing={this.isUpdateEditing}   dataresLocal={this.props.dataresLocal}/>
                                    </Sider>
                                    <div
                                        className='left-control'
                                        style={{ marginLeft: collapaseLeft === false ? '310px' : 0 }}
                                        onClick={this.foldLeftPanel}>
                                        {collapaseLeft ? <CaretRightOutlined className='control-btn' /> : <CaretLeftOutlined className='control-btn' />}
                                    </div>
                                </div>

                                {/*  */}
                                <VideoShow {...this.props} />

                                {/*  */}
                                <div className='right-content'>
                                    <div
                                        className='right-control'
                                        style={{ right: collapaseRight === false ? '330px' : 0 }}
                                        onClick={this.foldRightPanel}>
                                        {collapaseRight ? <CaretLeftOutlined className='control-btn' /> : <CaretRightOutlined className='control-btn' />}
                                    </div>
                                    <Sider
                                        className='right-sider'
                                        width={330}
                                        collapsedWidth={0}
                                        collapsed={collapaseRight}>
                                        <DataShowView
                                            className='data-show'
                                            intl={intl}
                                            data={data}
                                            editstate={this.props.editstate}
                                            editingCurrentFact={this.props.editingCurrentFact} />
                                    </Sider>
                                </div>
                            </Layout>

                            {/*  */}
                            <Footer className='edit-fact' onMouseUp={this.onMouseUp}>
                                {/* {<TimeLine {...this.props}/>} */}
                                <VideoSliceCardList isUpdateEditing={this.isUpdateEditing}/>
                            </Footer>
                        </div>
                    </Layout>
                </Spin>

            </div>
        )
    }
}